import { render, screen, waitFor, fireEvent } from '@testing-library/react';
import { jest, describe, it, expect, beforeEach } from '@jest/globals';
import { EventPreviewModal } from './EventPreviewModal';
import { useRouter } from 'next/navigation';

// Mock Next.js router
jest.mock('next/navigation');

const mockPush = jest.fn();

// Mock fetch
global.fetch = jest.fn() as jest.MockedFunction<typeof fetch>;

describe('EventPreviewModal', () => {
  const mockEvent = {
    id: 'event-123',
    title: 'Wedding Ceremony',
    date: '2024-06-15T16:00:00.000Z',
    time: '4:00 PM',
    location: 'Sunset Beach',
    description: 'Join us for our special day',
    activities: [
      { id: 'activity-1', title: 'Ceremony' },
      { id: 'activity-2', title: 'Reception' },
    ],
    slug: 'wedding-ceremony',
  };

  const mockRsvpStatus = {
    status: 'attending' as const,
    guestCount: 2,
  };

  beforeEach(() => {
    jest.clearAllMocks();
    (global.fetch as jest.MockedFunction<typeof fetch>).mockClear();
    
    // Setup useRouter mock
    (useRouter as jest.Mock).mockReturnValue({
      push: mockPush,
      replace: jest.fn(),
      refresh: jest.fn(),
      back: jest.fn(),
      forward: jest.fn(),
      prefetch: jest.fn(),
    });
  });

  it('should render modal with event data', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [mockRsvpStatus] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Wedding Ceremony')).toBeInTheDocument();
    });

    expect(screen.getByText('4:00 PM')).toBeInTheDocument();
    expect(screen.getByText('Sunset Beach')).toBeInTheDocument();
    expect(screen.getByText('Join us for our special day')).toBeInTheDocument();
  });

  it('should display activities list', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Activities')).toBeInTheDocument();
    });

    expect(screen.getByText('Ceremony')).toBeInTheDocument();
    expect(screen.getByText('Reception')).toBeInTheDocument();
  });

  it('should display RSVP status badge', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [mockRsvpStatus] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Attending')).toBeInTheDocument();
    });

    expect(screen.getByText('Guest count: 2')).toBeInTheDocument();
  });

  it('should show RSVP Now button when no RSVP exists', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('RSVP Now')).toBeInTheDocument();
    });
  });

  it('should navigate to full details page when View Full Details clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('View Full Details')).toBeInTheDocument();
    });

    fireEvent.click(screen.getByText('View Full Details'));

    expect(mockPush).toHaveBeenCalledWith('/event/wedding-ceremony');
    expect(onClose).toHaveBeenCalled();
  });

  it('should navigate to RSVP page when RSVP Now clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('RSVP Now')).toBeInTheDocument();
    });

    fireEvent.click(screen.getByText('RSVP Now'));

    expect(mockPush).toHaveBeenCalledWith('/guest/rsvp?eventId=event-123');
    expect(onClose).toHaveBeenCalled();
  });

  it('should close modal when close button clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Wedding Ceremony')).toBeInTheDocument();
    });

    const closeButton = screen.getByLabelText('Close');
    fireEvent.click(closeButton);

    expect(onClose).toHaveBeenCalled();
  });

  it('should close modal when backdrop clicked', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: mockEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    const { container } = render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Wedding Ceremony')).toBeInTheDocument();
    });

    // Click the backdrop (the outer div with fixed positioning)
    const backdrop = container.firstChild as HTMLElement;
    fireEvent.click(backdrop);

    expect(onClose).toHaveBeenCalled();
  });

  it('should display loading state while fetching data', () => {
    (global.fetch as jest.MockedFunction<typeof fetch>).mockImplementation(
      () => new Promise(() => {}) // Never resolves
    );

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    // Check for loading spinner
    const loadingSpinner = document.querySelector('.animate-spin');
    expect(loadingSpinner).toBeInTheDocument();
  });

  it('should display error message when fetch fails', async () => {
    (global.fetch as jest.MockedFunction<typeof fetch>).mockResolvedValueOnce({
      ok: false,
      json: async () => ({ success: false, error: { message: 'Failed to load event' } }),
    } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Failed to load event')).toBeInTheDocument();
    });
  });

  it('should handle event without optional fields', async () => {
    const minimalEvent = {
      id: 'event-123',
      title: 'Simple Event',
      date: '2024-06-15T16:00:00.000Z',
      activities: [],
    };

    (global.fetch as jest.MockedFunction<typeof fetch>)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: minimalEvent }),
      } as Response)
      .mockResolvedValueOnce({
        ok: true,
        json: async () => ({ success: true, data: [] }),
      } as Response);

    const onClose = jest.fn();
    render(<EventPreviewModal eventId="event-123" onClose={onClose} />);

    await waitFor(() => {
      expect(screen.getByText('Simple Event')).toBeInTheDocument();
    });

    // Should not display optional fields
    expect(screen.queryByText('Time')).not.toBeInTheDocument();
    expect(screen.queryByText('Location')).not.toBeInTheDocument();
    expect(screen.queryByText('Description')).not.toBeInTheDocument();
    expect(screen.queryByText('Activities')).not.toBeInTheDocument();
  });
});
